<template>
  <div class="invitation container-full">
    <header>
      <h1 class="text">{{ language.label_5_6 }}</h1>
      <p>{{ monthAmountFormat(language.tip_5_11) }}</p>
      <div class="flex jc-between ai-center">
        <m-button class="invite-btn hdr-el-blue" @click="showPopper(0)">
          {{ language.label_5_7 }}
        </m-button>
      </div>
    </header>
    <ul class="u1 flex jc-between">
      <li class="item-box">
        <div class="header flex jc-between ai-center">
          <span class="text">{{ language.invited }}</span>
          <m-button class="item-btn hdr-el-blue" @click="showPopper(1)" :height="36">
            {{ language.details }}
          </m-button>
        </div>
        <div class="flex ai-center">
          <span><b class="amount text">{{ myRankingList.length }}</b> <span>{{ language.suffix_1 }}</span></span>
        </div>
      </li>
      <li class="item-box">
        <div class="header flex jc-between ai-center">
          <span class="text">{{ language.label_5_10 }}</span>
        </div>
        <div class="flex ai-center">
          <span><b class="amount text">{{ calcDftAmount(shareInfo.credit).toFixed(2) }}</b> <span>DFT</span></span>
        </div>
      </li>
      <li class="item-box">
        <div class="header flex jc-between ai-center">
          <span class="text">{{ language.label_5_11 }}</span>
        </div>
        <div class="flex ai-center">
          <span><b class="amount text">{{ calcMonthlyProfit(shareInfo.spFromInvited).toFixed(2) }}</b> <span>DFV</span></span>
        </div>
      </li>
    </ul>
    <!--    全网助力排行榜-->
    <div class="list-container">
      <div class="tt flex jc-between ai-center text fs-18">{{ language.label_5_8 }}</div>
      <p class="flex jc-between ai-center">
        <span class="c1">{{ language.label_5_9 + language.address }}</span>
        <span class="c2">{{ language.label_5_10 }} DFT</span>
        <span class="c3">{{ language.label_5_11 }} DFV</span>
      </p>
      <div class="content">
        <p class="row flex jc-between ai-center text" v-for="item in allRankingList" :key="item.id">
          <span class="c1">{{ addressFormat(item.id) }}</span>
          <span class="c2">{{ calcDftAmount(item.creditFromInvited).toFixed(2) }}</span>
          <span class="c3">{{ calcMonthlyProfit(item.spFromInvited).toFixed(2) }}</span>
        </p>
      </div>
      <div v-if="allRankingLoading" class="mask flex jc-center ai-center"><i class="icon-loading"></i></div>
    </div>

    <m-popper v-model="showDialog">
      <template v-slot:title>
        {{ dialogType ? language.invited : language.label_5_7 }}
      </template>
      
      <div class="main-box">
        <div v-if="dialogType" class="popper-list">
          <p class="flex jc-between ai-center">
            <span class="c1">{{ language.label_5_9 + language.address }}</span>
            <span class="c2">{{ language.label_5_10 }} DFT</span>
            <span class="c3">{{ language.label_5_11 }} DFV</span>
          </p>
          <div class="content">
            <p class="row flex jc-between ai-center text" v-for="item in myRankingList" :key="item.id">
              <span class="c1">{{ addressFormat(item.id) }}</span>
              <span class="c2">{{ calcDftAmount(item.creditOfSuperior).toFixed(2) }}</span>
              <span class="c3">{{ calcMonthlyProfit(item.superiorSP).toFixed(2) }}</span>
            </p>
          </div>
        </div>
        <div v-else>
          <div class="invite-img relative" ref="poster">
            <img class="post d-b" src="../../images/member/power.png" alt="">
            <span class="t1 gold absolute">{{ language.bonus + language.reward }}</span>
            <span class="t2 gold absolute">{{ language.label_5_1 }}</span>
            <span class="t3 gold absolute">{{ language.airdrop + language.reward }}</span>
            <div class="card-box flex flex-column jc-between absolute">
              <h3 class="fs-16">{{ language.tip_4_5 }} DFT<br>{{ language.tip_5_12 }}</h3>
              <div class="ta-center">
                <span class="fs-12">{{ language.tip_5_13 }}</span><br>
                <b class="rate fs-30">{{ baseAPY.times(100).toFixed(2) }}%</b>
              </div>
            </div>
            <div class="qrcode absolute" ref="qrcode"></div>
            <img v-if="posterUrl" class="poster post d-b absolute" :src="posterUrl" alt="">
          </div>
          <div class="btn-row flex jc-center">
            <m-button class="action-btn hdr-el-blue text" ref="copy" :height="36">{{ language.label_5_13 }}</m-button>
          </div>
        </div>
      </div>
    </m-popper>

  </div>
</template>

<script>
import {mapGetters, mapState} from "vuex";
import MButton from "@/components/mButton";
import MPopper from "@/components/mPopper";
import {addressFormat} from "@/common/dataConvertor";
import {BN} from "@/common/util";
import ClipboardJS from "clipboard";
import html2canvas from 'html2canvas'

export default {
  name: "invitation",
  components: {MPopper, MButton},
  watch: {
    address() {
      this.getMyShareAndRanking();
    }
  },
  computed: {
    ...mapGetters(['language']),
    ...mapState('app', ['address']),
    baseAPY() {
      if (!this.vDEFX || !this.alphaInfo) return new BN(0);
      let {defxPerBlock, totalStakingPower} = this.vDEFX;
      let {newAlpha} = this.alphaInfo;
      const year_blocks = 28800 * 365;
      totalStakingPower = new BN(totalStakingPower);
      let rewardYear = new BN(defxPerBlock * year_blocks).times(1e18); //一年预计发放的基础奖励
      let stakedDefx = totalStakingPower.times(newAlpha).div(1e18); //用户总质押的dft(包含信用额度)
      return rewardYear.div(stakedDefx); //基础收益率
    },
  },
  data() {
    return {
      vDEFX: null,
      alphaInfo: null,
      shareRatio: 0,
      allRankingLoading: false,
      allRankingList: [],
      myRankingList: [],
      shareInfo: {
        credit: 0,
        spFromInvited: 0
      },
      dialogType: 0,
      showDialog: false,
      qrcode: null,
      clipboard: null,
      posterUrl:''
    }
  },
  methods: {
    addressFormat,
    calcDftAmount(credit) {
      let shareRatio = this.shareRatio;
      return shareRatio ? new BN(credit).div(shareRatio) : new BN(0)
    },
    calcMonthlyProfit(sp) {
      let {vDEFX} = this;
      let monthlyProfit = new BN(0);
      if (vDEFX) {
        let {defxPerBlock, totalStakingPower} = vDEFX;
        monthlyProfit = new BN(defxPerBlock).times(28800 * 30).times(sp).div(totalStakingPower).div(100)
      }
      return monthlyProfit
    },
    getAllRanking() {
      this.$api.vipvDefxInfo().then(({vDEFX}) => this.vDEFX = vDEFX);
      this.$api.getLatestAlpha().then(alphaInfo => this.alphaInfo = alphaInfo);
      this.allRankingLoading = true;
      this.$api.getAllRanking().then(({users}) => {
        this.allRankingList = users;
        this.allRankingLoading = false;
      }).catch(() => this.allRankingLoading = false);
      this.$api.getShareRatio().then((shareRatio) => this.shareRatio = new BN(shareRatio).div(1e18));
    },
    monthAmountFormat(text){
      let {baseAPY} = this;
      let dfv = baseAPY.div(12).times(1000*0.1).toFixed(2),dft = new BN(dfv).times(100).toFixed(2);
      return text.replace('{0}',dfv).replace('{1}',dft);
    },
    getMyShareAndRanking() {
      let {address} = this;
      if (!address) return;
      this.$api.getMyRanking(address).then(({users}) => this.myRankingList = users);
      this.$api.getMyInvitationInfo(address).then(({user}) => user && (this.shareInfo = user));
    },
    showPopper(dialogType) {
      this.dialogType = dialogType;
      this.showDialog = !this.showDialog;
      !dialogType && this.$nextTick(() => this.createQrcode());
    },
    createQrcode() {
      if (!this.address) return;
      let url = "http://defx.vip/#/vip/member?inviter=" + this.address;
      this.clipboard = new ClipboardJS(this.$refs.copy.$el, {text: () => url}).on('success', () => {
        console.debug(this.$toast)
        this.$toast.info(this.language.copy_tip)
        this.showDialog = false
      })
      this.qrcode = new window.QRCode(this.$refs.qrcode, {
        text: url,
        width: 110,
        height: 110,
      });
      this.$nextTick(() => {
        html2canvas(this.$refs.poster).then(canvas => {
          //此时就得到了dom元素转成了base64的图片
          this.posterUrl = canvas.toDataURL('image/png');
        })
      })
    }
  },
  mounted() {
    this.getAllRanking();
    this.getMyShareAndRanking();
  },
  unmounted() {
    this.clipboard?.destroy();
  }
}
</script>

